<template>
  <div class="content">
    <!--    -->
    <div class="user-info-box">
      <div v-if="isLoggedIn">
        <p><strong>{{ userId }}</strong></p>
        <p>: <strong>{{ membershipLevel }}</strong></p>
        <p> : <strong>{{ balance }}</strong></p>
        <p><a href="#"><strong>My Page</strong></a></p>
        <button @click="logout"></button>
      </div>
      <div v-else>
        <button @click="login"></button>
        <button @click="signup"></button>
      </div>
    </div>

    <!--   -->
    <div class="main-content">
      <!--    -->
      <div class="slider-container">
        <div class="slides" :style="{ transform: `translateX(-${currentSlide * 100}%)` }">
          <div v-for="(slide, index) in slides" :key="index" class="slide">
            <img :src="slide.img" :alt="'Slide ' + (index + 1)" /> <!--   -->
          </div>
        </div>
        <button class="prev" @click="prevSlide">
          <img src="@/assets/img/common/content-slide-icon-left-arrow.png" alt="Previous Slide" />
        </button>
        <button class="next" @click="nextSlide">
          <img src="@/assets/img/common/content-slide-icon-right-arrow.png" alt="Next Slide" />
        </button>
        <div class="indicators">
          <img v-for="(slide, index) in slides" 
            :key="index" 
            :src="currentSlide === index ? require('@/assets/img/common/content-slide-icon-filled-circle.png') : require('@/assets/img/common/content-slide-icon-empty-circle.png')"
            @click="goToSlide(index)"
            alt="Slide Indicator"
            class="indicator-img" />
        </div>
      </div>

      <!--    -->
      <div class="section">
        <h2> </h2>
        <div class="highlight-slider-container">
          <div class="highlight-slides" :style="{ transform: `translateX(-${currentHighlightSlide * 100}%)` }">
            <div v-for="(slide, index) in highlightSlides" :key="index" class="highlight-slide">
              <img :src="slide.img" :alt="'Highlight Slide ' + (index + 1)" />
              <img class="highlight-play-button" src="@/assets/img/common/content-slide-icon-play-button.png" alt="Play Button" />
              <div class="highlight-overlay"></div>
            </div>
          </div>
          <button class="highlight-prev" @click="prevHighlightSlide">
            <img src="@/assets/img/common/content-slide-icon-left-arrow.png" alt="Previous Slide" />
          </button>
          <button class="highlight-next" @click="nextHighlightSlide">
            <img src="@/assets/img/common/content-slide-icon-right-arrow.png" alt="Next Slide" />
          </button>
          <div class="highlight-indicators">
            <img v-for="(slide, index) in highlightSlides" 
              :key="index" 
              :src="currentHighlightSlide === index ? require('@/assets/img/common/content-slide-icon-filled-circle.png') : require('@/assets/img/common/content-slide-icon-empty-circle.png')"
              @click="goToHighlightSlide(index)"
              alt="Slide Indicator"
              class="highlight-indicator-img" />
          </div>
        </div>
      </div>

      <!--    -->
      <div class="section">
        <h2> </h2>
        <!-- Calendar   -->
        <Calendar :events="events" />
      </div>

    </div>
  </div>
</template>

<script>
import Calendar from '../common/CalendarMain.vue';

export default {
  name: 'AppContent',
  components: {
    Calendar
  },
  data() {
    return {
      //   
      isLoggedIn: true,  //    
      userId: 'user@mail.com',
      membershipLevel: 'Gold', // <---    
      balance: 10000,
      //   
      currentSlide: 0, //    
      slides: [
        { img: require('@/assets/img/common/content-top-slide-001.png') },
        { img: require('@/assets/img/common/content-top-slide-002.png') },
        { img: require('@/assets/img/common/content-top-slide-004.png') }
      ],
      //    
      currentHighlightSlide: 0, //      
      highlightSlides: [
        { img: require('@/assets/img/common/content-highlight-slide-009.png') },
        { img: require('@/assets/img/common/content-highlight-slide-006.png') },
        { img: require('@/assets/img/common/content-highlight-slide-002.png') }
      ],


      // Calendar    
      events: [
        {
          id: 1,
          team: 'blueFangs',
          location: '',
          time: '18:30',
          result: '',
          score: '6:3',
          date: '2024-10-23',
          isHomeGame: true //    
        },
        {
          id: 2,
          team: 'jumbos',
          location: '',
          time: '17:00',
          result: '',
          score: '2:5',
          date: '2024-10-24',
          isHomeGame: false //    
        }
        //  , axios - api   Calender*.vew   
      ]


    };
  },
  methods: {
    //   
    //   
    prevSlide() {
    if (this.currentSlide === 0) {
      this.currentSlide = this.slides.length - 1; //     
    } else {
      this.currentSlide--;
    }
    },
    //   
    nextSlide() {
      if (this.currentSlide === this.slides.length - 1) {
        this.currentSlide = 0; //     
      } else {
        this.currentSlide++;
      }
    },
    //   
    goToSlide(index) {
      this.currentSlide = index;
    },

    //    
    prevHighlightSlide() {
      if (this.currentHighlightSlide === 0) {
        this.currentHighlightSlide = this.highlightSlides.length - 1;
      } else {
        this.currentHighlightSlide--;
      }
    },
    nextHighlightSlide() {
      if (this.currentHighlightSlide === this.highlightSlides.length - 1) {
        this.currentHighlightSlide = 0;
      } else {
        this.currentHighlightSlide++;
      }
    },
    goToHighlightSlide(index) {
      this.currentHighlightSlide = index;
    }
  }
};
</script>

<style scoped>
.content {
  flex-grow: 1; /*       */
  padding-top: var(--header-height);  /* Header   */
  padding-bottom: var(--footer-height);  /* Footer   */
  /* max-width: 1200px; */
  /* margin: 30px auto; */
  /*   ,   30px */
  margin: 0 auto;
  margin-bottom: 100px;
  position: relative;
}

a {
  color: #393939;
}

/*    */
.user-info-box {
  position: absolute;
  top: 630px;
  right: 100px;
  background-color: #f8f9fa;
  width: 220px;
  height: 200px;
  padding: 10px 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  text-align: right;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 100;
}

.user-info-box button {
  background-color: #60a191;
  color: white;
  border: none;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
}

.user-info-box button:hover {
  background-color: #4f8578;
}

.user-info-box p {
  margin: 5px 5px;
}

.user-info-box a:hover {
  text-decoration: underline;
}

/*    */
.slider-container {
  position: relative;
  width: 100%;
  height: 400px;
  margin: 0 auto;
  overflow: hidden;
}

.slides {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.slide {
  position: relative;
  min-width: 100%;
  height: 400px;
}

.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.prev img,
.next img {
  width: 30px;
  height: 30px;
}

.prev, .next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}

.indicators {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.indicator-img {
  width: 10px;
  height: 10px;
  margin: 0 5px;
  cursor: pointer;
}

/*  */
.section {
  margin-top: 50px;
  text-align: center;
}

/*     */
.highlight-slider-container {
  position: relative;
  width: 500px;
  height: auto;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 15px;
}

.highlight-slides {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.highlight-slide {
  position: relative;
  min-width: 100%;
  height: auto;
}

.highlight-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
}

/*     */
.highlight-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); 
}

/*   */
.highlight-play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px !important;
  height: 50px !important;
  z-index: 10;
  cursor: pointer;
}

.highlight-prev img,
.highlight-next img {
  width: 30px;
  height: 30px;
}

.highlight-prev, .highlight-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.highlight-prev {
  left: 10px;
}

.highlight-next {
  right: 10px;
}

.highlight-indicators {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.highlight-indicator-img {
  width: 10px;
  height: 10px;
  margin: 0 5px;
  cursor: pointer;
}
</style>