<template>
  <div class="game-results-page">
    <!--  1      -->
    <LogoHeader />

    <!--  ,    -->
    <div class="menu">
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/game/schedule' }" @click="navigateTo('/game/schedule')"> </div>
      <div class="menu-item" :class="{ 'active-menu-item': activeMenu === '/game/results' }" @click="navigateTo('/game/results')"> </div>
    </div>
  
    <!--     -->
    <div class="game-results-content">



    </div>
  </div>
</template>

<script>
import LogoHeader from '../common/LogoHeader.vue';

export default {
    name: 'AppResults',
    components: {
      LogoHeader
  },    
  data() {
    return {
      activeMenu: this.$route.path //   
    };
  },
  watch: {
    //    activeMenu 
    $route(to) {
      this.activeMenu = to.path;
    }
  },
  methods: {
    navigateTo(route) {
      this.$router.push(route);
      this.activeMenu = route; //      
    }
  }
}
</script>

<style>
.game-results-page {
    padding-top: var(--header-height);
    padding-bottom: var(--footer-height);
    text-align: center;
}

/*  */
.menu {
  display: flex;
  justify-content: center;
  gap: 50px;
  padding-bottom: 20px;
}

.menu-item {
  width: 120px;
  padding: 10px 0;
  position: relative;
  cursor: pointer;
  font-weight: bold;
  color: #565656;
}

.menu-item::after {
  content: "";
  position: absolute;
  bottom: -5px; /*       */
  left: 0;
  width: 100%;  /*   div   */
  height: 3px;
  background-color: black;
  transform: scaleX(0); /*    */
  transition: transform 0.3s ease;
}

.active-menu-item {
  color: black;  /*     black  */
}

.active-menu-item::after {
  transform: scaleX(1);  /*     */
}

.menu-item:hover::after {
  transform: scaleX(1);  /*     */
}

/*   */

</style>